body {
    margin: 0 auto;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
  
  #page_acceuil {
    width: 100%;
    height: 100vh;
    max-height: 90%;
    /* background: red; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  #page_acceuil .a_la_une_et_astuce {
    width: 100%;
    height: 50vh;
    /* background: purple; */
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  #page_acceuil .a_la_une_et_astuce .a_la_une {
    width: 65%;
    height: 100%;
    background: rgb(255, 255, 255);
    border: 1px solid black;
    border-radius: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    
  }
  #page_acceuil .a_la_une_et_astuce .a_la_une .plus_information {
    width: 100%;
    /* background: rgb(142, 243, 11); */
    height: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 30px;
  }

  #page_acceuil .a_la_une_et_astuce .a_la_une h1 {
    width: 85%;
    height: auto;
    font-size: 4.5vw;
    /* background: bisque; */
    margin-left: 30px;
    line-height: 1;
    margin-bottom: 0;
  }
  #page_acceuil .a_la_une_et_astuce .astuce {
    width: 25%;
    height: 100%;
    background: rgb(255, 255, 255);
    border: 1px solid black;
    border-radius: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 30px;
  }
  #page_acceuil .a_la_une_et_astuce .astuce .titre_astuce {
  margin: 0;
  
  }

  #page_acceuil .a_la_une_et_astuce .astuce .list_astuce {
    width: 90%;
    height: 60%;
    display: flex;
    flex-direction: column;

    overflow: auto;
    scrollbar-width: thin; /* Pour Firefox */
    scrollbar-color: #8BC462 #f0f0f0; /* Couleur de la barre et du fond de la barre pour Firefox */

    /* Styles spécifiques pour WebKit (Chrome, Safari) */
    &::-webkit-scrollbar {
        width: 8px; /* Largeur de la barre de défilement */
    }

    &::-webkit-scrollbar-thumb {
        background-color: #8BC462; /* Couleur de la poignée de la barre de défilement */
    }

    &::-webkit-scrollbar-track {
        background-color: #f0f0f0; /* Couleur du fond de la barre de défilement */
    }
  }
  #page_acceuil .a_la_une_et_astuce .astuce .list_astuce :hover{
   border: none;
   border-radius: 20px;
  }

  
  #page_acceuil .a_la_une_et_astuce .astuce .voir_plus_astuce {
    /* display: flex;
    align-items: center;
    background: rgb(252, 144, 144);
    width: 80%; */
    width: 100%;
    cursor: pointer;
    /* background: white; */
  }
  #page_acceuil .list_cultures_section {
    width: 100vw;
    height: 320px;
    /* background: rgb(38, 70, 214); */
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
   
  }
  #page_acceuil .list_cultures_section .list_cultures_bottom {
    width: 94%;
    height: 100%;
    display: flex;
    /* background: wheat; */
    flex-direction: column;
    justify-content: space-around;
  }
  #page_acceuil .list_cultures_section .list_cultures_bottom .culture_go {
    display: flex;
    /* background: brown; */
    margin-left: 30px;
  }
  #page_acceuil .list_cultures_section .list_cultures_bottom .culture_go h1 {
    margin: 0;
    font-size: 2vw;
    margin-right: 30px;
  }
  #page_acceuil .list_cultures_section .list_cultures_bottom .culture_go .link_go {
    height: 100%;
    width: 100%;
    cursor: pointer;
    /* background: white; */
  }
 
  #page_acceuil .list_cultures_section .list_cultures_bottom .list_culture {
    width: 100%;
    height: 220px;
    display: flex;
    align-items: flex-start;
    column-gap: 2%;
    justify-content: space-between;
    overflow-x: auto;
    scrollbar-width: thin; /* Pour Firefox */
    scrollbar-color: #8BC462 #f0f0f0; /* Couleur de la barre et du fond de la barre pour Firefox */

    /* Styles spécifiques pour WebKit (Chrome, Safari) */
    &::-webkit-scrollbar {
        width: 8px; /* Largeur de la barre de défilement */
    }

    &::-webkit-scrollbar-thumb {
        background-color: #8BC462; /* Couleur de la poignée de la barre de défilement */
    }

    &::-webkit-scrollbar-track {
        background-color: #f0f0f0; /* Couleur du fond de la barre de défilement */
    }
   
  }
 
